"use client";

import React from "react";
import { Box, Input, IconButton, Typography, Button } from "@mui/joy";
import Image from "next/image";
import Search from "@mui/icons-material/Search";
import ShoppingCartOutlined from "@mui/icons-material/ShoppingCartOutlined";
import NotificationsNoneOutlined from "@mui/icons-material/NotificationsNoneOutlined";
import LoginOutlined from "@mui/icons-material/LoginOutlined";
import Divider from "@mui/joy/Divider";

import logo from "../../../assets/images/logo-red.png"; // مسیر لوگوی برند شما

export default function Header() {
  return (
    <Box
      component="header"
      sx={{
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between",
        px: { xs: 2, md: 4 },
        py: 1.5,
        bgcolor: "#fff",
        boxShadow: "0 1px 6px rgba(0,0,0,0.08)",
        position: "sticky",
        top: 0,
        zIndex: 1000,
      }}
    >
      {/* ---------- لوگو و برند ---------- */}
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          gap: 1,
          cursor: "pointer",
          minWidth: "fit-content",
        }}
      >
        <Image
          src={logo}
          alt="Brand Logo"
          width={44}
          height={44}
          priority
          style={{ objectFit: "contain" }}
        />
        <Typography
          level="h4"
          sx={{
            fontWeight: 800,
            color: "#E60023",
            fontSize: { xs: "1.2rem", md: "1.5rem" },
          }}
        >
          برند من
        </Typography>
      </Box>

      {/* ---------- نوار جستجو ---------- */}
      <Box
        sx={{
          flex: 1,
          display: "flex",
          justifyContent: "center",
          px: { xs: 2, md: 4 },
        }}
      >
        <Input
          startDecorator={<Search />}
          placeholder="جستجو در محصولات..."
          sx={{
            width: { xs: "100%", sm: "90%", md: "75%" },
            bgcolor: "#f5f5f5",
            borderRadius: 12,
            height: 42,
            fontSize: "0.95rem",
            "--Input-focusedHighlight": "#E60023",
            "&:focus-within": {
              boxShadow: "0 0 0 2px rgba(230,0,35,0.2)",
            },
          }}
        />
      </Box>

      {/* ---------- آیکون‌ها و دکمه ورود ---------- */}
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          gap: 1.5,
          minWidth: "fit-content",
        }}
      >
        <IconButton size="lg" variant="plain" color="neutral">
          <NotificationsNoneOutlined />
        </IconButton>

        <Divider orientation="vertical" sx={{ height: 24 }} />

        <IconButton size="lg" variant="plain" color="neutral">
          <ShoppingCartOutlined />
        </IconButton>

        <Button
          variant="outlined"
          color="neutral"
          size="md"
          startDecorator={<LoginOutlined />}
          sx={{
            borderRadius: "md",
            fontWeight: 500,
            color: "text.primary",
            borderColor: "#e0e0e0",
            "&:hover": { bgcolor: "#f0f0f0" },
          }}
        >
          ورود | ثبت‌نام
        </Button>
      </Box>
    </Box>
  );
}
